<template>
  <div class="div-class-echartsTable">
		<!-- dataupdated -->
		<div style="display:none">{{ indexValue }}</div>
    <div id="myChart" :style="{width: '100%', height: '400px'}"></div>
  </div>
</template>

<script>
// 
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/bar')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
import axios from 'axios';
// 
import eventdata from './event.js'

export default {
	name: 'echartsTable',
	data () {
		return {
			indexValue: 0,
		}
	},
	
	mounted() {
		this.drawLine();
	},
	updated () {


		// drawLineupdated
		this.drawLine()
		console.log('updatedindexValue: '+this.indexValue)
	},
	created () {
    eventdata.$on('getDataClick', data => {
			this.indexValue = data
		});
	},

 	
  methods: {
// 
    drawLine() {
			console.log('echartsTable drawLineindexValue: '+this.indexValue)
			// domecharts
			 let myChart = echarts.init(document.getElementById('myChart'))
      // 
			// 
			//axios
      myChart.setOption({
		    title: {
		      text: '20186',
		      left: 'center',
		      subtext: ''
		    },
		    //
		    tooltip: {
		      trigger: 'axis',
		      axisPointer: {
		        type: 'shadow'
		      }
		    },
		    grid: {
		      left: '3%',
		      right: '4%',
		      bottom: '3%',
		      containLabel: true
		    },
		    xAxis: {
		      type: 'value',
		      boundaryGap: [0, 0.01]
		    },
		    yAxis: {
					type: 'category',
					//json
					data:[]
		      // data: ['','','','','','','','3','A','','2'],
		    },
		    //namelegend,
		    series: [
		      {
						type: 'bar',
						//json						
						data:[],
            //  data: [18759, 5089, 5974, 7163, 7295, 9286,13545,19878,20705,27873,109655],
            itemStyle:{
              normal:{
                color:'rgb(79, 184, 249)'
              }
            },
		      }
				],
		  });
				// // 
      	// myChart.setOption(option);
			axios.get('https://api.myjson.com/bins/1f06ka')
			// axios.get('../static/echartsData.json')//
			.then(res=>{
				// this.xydata=res.data;dataasiosjson
				const data = res.data
				this.goods = data
				// .then
				myChart.setOption({
					
					title: {
						text: this.goods[this.indexValue].text
					},
					yAxis: {
						//json
						data:this.goods[this.indexValue].movie
					},
					//namelegend,
					series: [
						{
							//json				
							data:this.goods[this.indexValue].value
						}
					],
				});
			})
			.catch(error=>{
				alert('')
			});
				
		},
	},
}
</script>

<style lang="stylus" scoped>
</style>


