<template>
    <div>
        <div class="div-class-timebar">
            <!-- <button @click="handleClick"></button> -->
            <span></span>
            <select id="selDateElm" v-on:change="getMonthValue()">
                    <option  value="2018-06-01">20187</option>
                    <option  value="2018-06-01">20186</option>
                    <option  value="2018-05-01">20185</option>
                    <option  value="2018-04-01">20184</option>
                    <option  value="2018-03-01">20183</option>
                    <option  value="2018-02-01">20182</option>
                    <option  value="2018-01-01">20181</option>
            </select>
        </div>
    </div>
</template>
<script>
// 
import eventdata from './event.js'
export default {
    name:'timeBar',
    methods: {
        getMonthValue(){
            var myselect=document.getElementById("selDateElm");
            this.index=myselect.selectedIndex
            eventdata.$emit('getDataClick', this.index)
            console.log('timeBar'+this.index)
        },
    }
}
</script>
<style lang="stylus" scoped>
    .div-class-timebar
        display flex
        left 0
</style>


